<script>
   /****************************************************************/
   /* PlotExtremes                                                 */
   /* -----------------------------------------                    */
   /* shows extremes plot for DD-SIMCA results                     */
   /****************************************************************/

   import { Vector } from 'mdatools/arrays';
   import { qchisq } from 'mdatools/distributions';
   import { Segments, Legend, Lines } from 'svelte-plots-basic/2d';
   import { LINE_COLORS, FACE_TCOLORS, MARKERS } from '../../../shared/plots/Defaults.js';

   import PlotSeries from '../../../shared/plots/PlotSeries.svelte';
   import Axes from '../../../shared/plots/Axes.svelte';

   let {
      params,                        // JSON with main plot parameters
      props,                         // JSON with main properties (models, results, datasets, etc. formed in AppPlots)
   } = $props()

   // constants
   const ciColor = '#e0e0e0';
   const xLabel = 'Number of extremes (expected)';
   const yLabel = 'Number of extremes (observed)';
   const fileName='plot-extremes';

   // function to compute elements of confidence ellipse
   function getCI(n) {

      // here we add (0, 0) point hence n + 1
      const xStart = Vector.zeros(n + 1);
      const yStart = Vector.zeros(n + 1);
      const xEnd = Vector.zeros(n + 1);
      const yEnd = Vector.zeros(n + 1);

      for (let i = 1; i < n; i++) {
         const a = i / n;
         const em = 2 * Math.sqrt(i * (1 - a));
         xStart.v[i] = i;
         xEnd.v[i] = i;
         yStart.v[i] = i - em;
         yEnd.v[i] = i + em;
      }

      xStart.v[n] = n;
      xEnd.v[n] = n;
      yStart.v[n] = n;
      yEnd.v[n] = n;

      return {xStart, xEnd, yStart, yEnd};
   }

   // function to compute the observed extremes
   function getObserved(res, outcomes, Nf) {
      const fa = outcomes.f;
      const n = res.nMembers;
      const y = Vector.zeros(n);
      for (let i = 1; i <= n; i++) {
         const alpha = i / n;
         const eCrit = qchisq(1 - alpha, Nf);
         let sum = 0;
         for (let j = 0; j < n; j++) {
            if (fa[res.ind.members[j]] > eCrit) sum += 1;
         }
         y.v[i - 1] = sum;
      }
      return y;
   }

   // get the properties needed for the plot
   let res = $derived(props && props.res ? props.res : null);
   let nimca = $derived(props && props.model ? props.model : null);
   let Nf = $derived(nimca && nimca.Nf ? nimca.Nf: 0);

   // get plot parameters (results to show and legend position)
   let resIDsAll = $derived(Object.keys(res));
   let resIDs = $derived(params.resID !== 'both' ? [params.resID] : resIDsAll);
   let legendPosition = $derived(params.legendPosition ? params.legendPosition : 'bottomright');


   // create legend items based on resIDs array
   let legendItems = $derived(resIDs ? resIDs.map(v => ({
      id: v,
      label: v,
      point: {
         marker: 1,
         lineColor: LINE_COLORS[v],
         faceColor: FACE_TCOLORS[v],
         marker: MARKERS[v],
         markerSize: 0.85,
         lineWidth: 1.5
      },
   })) : null);

   // compute x-coordinates for each result object
   let xValues = $derived(res && resIDs ? resIDs.reduce((acc, id) => {
      acc[id] = Vector.seq(1, res[id].nMembers);
      return acc
   }, {}) : null);

   // compute y-coordinates for each result object
   let yValues = $derived(res && resIDsAll ? resIDsAll.reduce((acc, id) => {
      acc[id] = getObserved(res[id], res[id].outcomes, Nf);
      return acc;
   }, {}) : null);

   // get largest number of values in the series
   let aMax = $derived(resIDs ? resIDs.reduce((acc, id) => Math.max(acc, xValues[id].length), 0) : 1);

   // compute confidence interval
   let ci = $derived(aMax > 1 ? getCI(aMax) : null);

   // compute axis limits
   let limX = $derived(xValues ? [-1, aMax + 2] : [0, 1]);
   let limY = $derived(xValues ? [-1, aMax + 2] : [0, 1]);

   // other plot parameters
   let title = 'Extremes';
</script>

{#if res }

<Axes {title} {xLabel} {yLabel} {limX} {limY} {fileName} whole={true}>

   {#if ci}
   <Segments {...ci} lineColor={ciColor} />
   <Lines xValues={ci.xStart} yValues={ci.yStart} lineColor={ciColor} />
   <Lines xValues={ci.xEnd} yValues={ci.yEnd} lineColor={ciColor} />
   {/if}

   <!-- data points with statistics -->
   {#each resIDs as id, i}
      <PlotSeries
         xValues={xValues[id]}
         yValues={yValues[id]}
         plotType='p'
         showLabels={false}
         faceColor={FACE_TCOLORS[id]}
         mainColor={LINE_COLORS[id]}
         marker={MARKERS[id]}
         lineWidth={1.5}
      />
   {/each}


   {#if legendItems && legendItems.length > 0}
   <Legend items={legendItems} position={legendPosition} />
   {/if}
</Axes>
{/if}