<script>
   /*************************************************************/
   /* Root component for the application                        */
   /*************************************************************/

   import { date2str } from '../../shared/utils';

   import TrainAppContainer from './tab_train/AppContainer.svelte';
   import TestAppContainer from './tab_test/AppContainer.svelte';
   import NewAppContainer from './tab_new/AppContainer.svelte';
   import App from '../../shared/components/MainApp.svelte';
   import WASM from '../../shared/wasm/dist/pca_wasm.js';

   /* callback to check validity of JSON with session */
   function checkSession(s) {

      const keys1 = Object.keys(s);
      if (!['date', 'tab', 'train', 'test', 'new'].every(v => keys1.includes(v))) return false;

      const keys2 = Object.keys(s.train);
      if (!['nimca', 'calset', 'calres', 'outliers', 'prepType',
         'prepmodel', 'prepmeta', 'prepobj'].every(v => keys2.includes(v))) return false;

      const keys3 = Object.keys(s.test);
      if (!['nimca', 'prep', 'testset', 'testres', 'outliers'].every(v => keys3.includes(v))) return false;

      const keys4 = Object.keys(s.new);
      if (!['nimca', 'prep', 'newset', 'newres', 'outliers'].every(v => keys4.includes(v))) return false;

      return true;
   }

   /* callback to initialize new session */
   function initSession() {

      return {
         date: date2str(new Date()),
         tab: 0,
         train: {
            nimca: 0,
            calset: 0,
            calseto: 0,
            calsetp: 0,
            calres: 0,
            outliers: [],
            prepType: null,
            prepmodel: 0,
            prepmeta: 0,
            prepobj: [],
         },
         test: {
            nimca: 0,
            prep: 0,
            testset: 0,
            testseto: 0,
            testres: 0,
            outliers: [],
            ind: null,
         },
         new: {
            nimca: 0,
            prep: 0,
            newset: 0,
            newres: 0,
            outliers: []
         }
      }
   }

   // app containers
   const tabs = [
      {name: 'Train', label: 'train', component: TrainAppContainer},
      {name: 'Test', label: 'test', component: TestAppContainer},
      {name: 'Predict', label: 'new', component: NewAppContainer},
   ]
</script>

<App
   appID="nimca"
   appTitle="NIMCA"
   appShortTitle="NIMCA"
   appSlogan="NaÃ¯ve DD-SIMCA classifier"
   {WASM}
   {tabs}
   {initSession}
   {checkSession}
>

   {#snippet footer()}
      <p>Current release: <span>1.2.0</span>
      &nbsp;Feedback: <a class="paper-link" href="mailto:help@mda.tools?subject=NIMCA app">help@mda.tools</a>
      </p>
   {/snippet}

   {#snippet info()}
   <h2>NIMCA</h2>
   <p>Authors: <em>Oxana Rodionova, Alexey Pomerantsev, Sergey Kucheryavskiy</em></p>
   <p>
      Interactive web-application for creating and using NaÃ¯ve DD-SIMCA (NIMCA) one class classifier
      directly in your browser. The app does not send any information to a server,
      all calculations are done locally on your device.
   </p>
   <p>Check  <a href="">paper</a></p>
   <h3>v. 1.2.0</h3>
   <p class="release-info">released 24/11/2025</p>
   <ul>
      <li>Corrected help text in Train tab.</li>
      <li>Dataset plot now show centered/scaled values.</li>
   </ul>
   {/snippet}
</App>

