<script>
   /**************************************************************/
   /* SIMCASettings                                              */
   /* -------------------------------------                      */
   /* shows panel with SIMCA model settings.                     */
   /**************************************************************/

   import { Select, Container, Switch, Range } from 'svelte-controls-basic';
   import { NIMCA } from '../utils.svelte.js';

   let {
      calset,                    // JSON with calibration set
      isAlt = false,             // logical, is the widget shown for alternative classes only
      fit = true,                // logical, is widget used for model fitting or not
      nimca = $bindable(),       // object with NIMCA model
   } = $props();



   // possible choices for alpha and gamma
   const alphaOptions = [10, 5, 1, 0.1];
   const gammaOptions = [5, 1, 0.1, 0.01];

   let center = $state(nimca ? nimca.center : true);
   let scale = $state(nimca ? nimca.scale : false);

   $effect(() => {
      if (fit && calset) {
         nimca = NIMCA.fit(calset, center, scale);
      }
   });

</script>

{#if nimca && nimca.className}
<Container name="target-class" label="">
   <p class="title">Target class: {nimca.className}</p>
</Container>

{#if fit}
<Container name="center" label="Center">
   <Switch id="center" bind:value={center} options={["no", "yes"]} />
</Container>

<Container name="scale" label="Scale">
   <Switch id="scale" bind:value={scale} options={["no", "yes"]} />
</Container>
{/if}

<Container name="alpha" label='Alpha, %:'>
   <Select bind:value={nimca.alpha} options={alphaOptions} />
</Container>

{#if !isAlt}
<Container name="gamma" label='Gamma, %:'>
   <Select bind:value={nimca.gamma} options={gammaOptions} />
</Container>
{/if}

<Container name="limits" label='Limits:'>
   <Select bind:value={nimca.limType} options={['classic', 'robust']} />
</Container>
{/if}
