<script>
   /*************************************************************/
   /* AppContainer for training                                 */
   /* -------------------------                                 */
   /* app container for training of DD-SIMCA model              */
   /*************************************************************/

   import { untrack } from 'svelte';
   import { Container, Button } from 'svelte-controls-basic';
   import { downloadResults, downloadModel, NIMCA } from '../utils.svelte';

   import { replacer, excludeOutliers, reviver, seq } from '../../../shared/utils.js';

   import Outliers from '../../../shared/widgets/Outliers.svelte';
   import PrepWidget from '../../../prep/src/shared/PrepWidget.svelte';

   import DataLoader from '../../../pca/src/shared/DataLoader.svelte';

   import ModelTable from '../shared/ModelTable.svelte';
   import NIMCASettings from '../shared/NIMCASettings.svelte';

   import ResultTable from './ResultTable.svelte';
   import AppPlots from './AppPlots.svelte';

   // WASM promise and session object from parent app
   let {WASM, backupSession, session = $bindable()} = $props();

   // get part of the session for this tab
   const s = $derived(session['train']);

   // if NIMCA model is not instance of NIMCA, recreate it
   $effect(() => {
      if (s.nimca && !(s.nimca instanceof NIMCA)) {
         s.nimca = NIMCA.fromJSON(s.nimca);
      }
   });


   function resetAll() {
      s.nimca = null;
      s.calres = null;
      s.calsetp = null;
      s.prepmodel = null;
   }


   // callback function to set object with a give position in plot series
   // as outlier
   function markOutlier(pos) {

      // check that position is valid
      if (pos < 0 || pos > s.calres.nrows - 1) return;

      // find sample ID corresponding to the position
      // this is needed if some of the objects already
      // removed from results as outliers
      const id = s.calseto.objindices[pos];

      // add the object to outliers array and reset the model and all related outcomes
      if (id >= 0 && !s.outliers.includes(id)) {
         s.nimca = null;
         s.outliers.push(id);
         s.calseto = excludeOutliers(s.calset, s.outliers);
         resetAll()
      }
   }

   // callback to reset outliers
   function resetOutliers() {
      s.nimca = null;
      s.outliers = [];
      s.calseto = s.calset;
      resetAll();
   }

   // callback to undo outliers
   function undoOutliers() {
      s.nimca = null;
      s.outliers.pop();
      s.calseto = excludeOutliers(s.calset, s.outliers);
      resetAll();
   }

   // save local hashes
   let nimcaHash = $derived(s.nimca && s.nimca.hash !== nimcaHash ? s.nimca.hash : 0);

   // pre-rendering effect to avoid bad structure of the session object
   $effect.pre(() => {

      // if training set is set null - reset model and all related parameters
      if (s.calset === null) {
         s.outliers = [];
         s.calsetp = null;
         s.calseto = null;
         s.className = null;
         s.prepmodel = null;
         s.prepobj = [];
         s.prepType = null
         s.nimca = null;
      } else if (s.calset && !s.className) {
         s.className = s.calset.classNames[0];
         s.calseto = s.calset;
      }
   });

   // if training set is set null - reset model and all related parameters
   $effect(() => {
      if (s.calset === null) {
         s.nimca = null;
         s.calseto = null;
         s.calsetp = null;
      }
   });

   // if training set is set null - reset model and all related parameters
   $effect(() => {
      if (s.calsetp === null) {
         s.nimca = null;
         s.calres = null;
      }
   });


   $effect(() => {
      if (s.calsetp && s.nimca) {
         s.calres = s.nimca.predict(s.calsetp);
      }
   });

   $effect(() => {
      const sessionStr = untrack(() => JSON.stringify(session, replacer));
      if (s.nimca) {
         backupSession(sessionStr, s.calset, s.nimca);
      } else {
         backupSession(sessionStr, s.calset);
      }
   });

</script>


<div class="app-container">

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="data-loader">
         <DataLoader bind:dataset={s.calset} className="Class" classMandatory={true} classUnique={true}/>
      </div>

      {#if s.outliers && s.outliers.length > 0 }
      <div class="outliers-pane">
         <Outliers outliers={s.outliers} dataset={s.calset} {resetOutliers} {undoOutliers} />
      </div>
      {/if}

      {#if s.calseto }
         <PrepWidget
            {WASM}
            rawdata={s.calseto}
            bind:prepType={s.prepType}
            bind:prepobj={s.prepobj}
            bind:prepdata={s.calsetp}
            bind:prepmeta={s.prepmeta}
            bind:prepmodel={s.prepmodel}
         />
      {/if}


      <div class="nimca-settings">

         {#if s.calsetp }
         <NIMCASettings
            className={s.className}
            calset={s.calsetp}
            bind:nimca={s.nimca}
         />
         {/if}

         {#if s.nimca && s.calres}
         <Container label='Download'>
            <div class="buttons">
            <Button onclick={() => downloadResults(s.nimca, s.calres, s.calset, "train", s.outliers)} text="Res (cal)"/>
            <Button onclick={() => downloadModel(s.nimca, s.prepmodel)} text="Model" />
            </div>
         </Container>
         {/if}
      </div>

      {#if s.nimca && s.calres}
      <div class="model-stats">
         <ModelTable nimca={s.nimca} />
      </div>
      <div class="result-stats">
         <ResultTable calres={s.calres} />
      </div>
      {/if}
   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !s.nimca || !s.calres}
         <div class="help-text">
         {#if !s.calset}
            <p>
               In this tab you can train a NaÃ¯ve DD-SIMCA (NIMCA) model. After creating the model you can save it to a file and use it in other tabs. For example, validate it using test set (tab <strong>Test</strong>) or apply it to a dataset with unknown class membership (tab <strong>Predict</strong>).
            </p>
            <p><strong>Step 1.</strong></p>
            <p>Load your training set from a CSV file. The file should contain data as numeric values delimited
               by comma (with dot as decimal separator) or by semicolon (with comma as decimal separator). Rows correspond to observations or objects, columns â€” to variables.
            </p>
            <p>
               First column must contain class names for each row. All rows in the training set should belong to the target class, so it is expected that the class names are the same (you will get error message if not).
            </p>
            <p>
               First row can contain variable names (<strong>header</strong>) and first column can contain labels for the objects/observations (<strong>row labels</strong>) â€” just set the corresponding options after selecting the file. In this case it is assumed that column with the class names is located next to the column with row labels. Here is an example:
            </p>
            <div>
               <table>
               <thead>
               <tr>
                  <td>Name</td>
                  <td>Class</td>
                  <td>x1</td>
                  <td>x2</td>
                  <td>x3</td>
               </tr>
               </thead>
               <tbody>
               <tr>
                  <td>O1</td>
                  <td>A</td>
                  <td>3.8</td>
                  <td>1.3</td>
                  <td>13.6</td>
               </tr>
               <tr>
                  <td>O2</td>
                  <td>A</td>
                  <td>11.1</td>
                  <td>12.3</td>
                  <td>3.3</td>
               </tr>
               <tr>
                  <td>O3</td>
                  <td>A</td>
                  <td>1.2</td>
                  <td>2.3</td>
                  <td>4.2</td>
               </tr>
               </tbody>
               </table>
            </div>
            <p></p>
         {:else}
            {#if !s.calsetp}
            <p><strong>Step 2</strong></p>
            <p>
               Next step is to apply proper preprocessing methods. You can create a chain of the methods directly in the app
               (button <strong>Create</strong>), load previously created preprocessing model (<strong>Load</strong>), or just skip this step. In this case the data will be only mean centered for computing the distances.
            </p>
            {/if}
         {/if}
         </div>
      {:else if s.nimca && s.calres  }
         {#key s.calres }
         <AppPlots {markOutlier} {...s} />
         {/key}
      {/if}
   </div>
</div>

