<script>
   /*************************************************************/
   /* ModelLoader                                               */
   /* -------------------------                                 */
   /* loads NIMCA model from file                               */
   /*************************************************************/

   import { FileSelector, ButtonCancel, Container, Button } from 'svelte-controls-basic';
   import { reviver } from '../../../shared/utils';
   import { NIMCA } from '../utils.svelte';

   import PrepInfo from '../../../prep/src/shared/PrepInfo.svelte';

   let { prep = $bindable(), nimca = $bindable() } = $props();    // object with model


   // string with model file name
   let modelFile = $state('');

   // app status for handling and showing errors
   let status = $state('neutral');
   let errMsg = $state('');

   // gets model content as one large string, parse it into matrix
   function setModel(strData) {
      try{
         const m = JSON.parse(strData, reviver);

         if (!Object.keys(m).includes('nimca') ||  !Object.keys(m).includes('prep') || m.nimca.class[0] !== 'nimcamodel') {
            throw new Error('The JSON file you selected does not contain NIMCA mdel.');
         }

         m.nimca['fileName'] = modelFileName;
         prep = m.prep;
         nimca = NIMCA.fromJSON(m.nimca);
         status = 'success';
         errMsg = ''
      } catch (e) {
         nimca = null;
         prep = null;
         status = 'error';
         errMsg = e;
      }
   }

   // promise for reading the CSV file
   function readFile (e) {
      const reader = new FileReader();
      reader.onload = () => { setModel(reader.result) };
      reader.readAsText(modelFile);
   }

   function reset() {
      modelFile = '';
      nimca = null;
      prep = null;
      status = 'neutral';
      errMsg = '';
   }

   // reactive expression for tracking changes of 'modelFile'
   $effect(() => {
      if (modelFile === null) {
         // if model file is empty reset status and error messages
         nimca = null;
         prep = null;
         status = 'neutral';
         errMsg = '';
      } else {
         // if not just remove error messages if any
         status = 'neutral';
         errMsg = '';
      }
   });

   // file name with model without extension
   let modelFileName =$derived(modelFile ? modelFile.name.replace(/\.[^/.]+$/, "") : '');
</script>

{#if !nimca }

   <!-- show if data is not loaded -->
   <!-- reset component if dataFile is changed inside -->
   {#key modelFile}
   <Container>
      <FileSelector id="modelFile" bind:file={modelFile} acceptType=".json" message="Select file with model"/>
   </Container>
   {/key}

   {#if modelFile}
   <Container label="&nbsp;">
      <Button onclick={readFile} text="Load" />
   </Container>
   {/if}
{:else}

   <!-- dataset is loaded - show info and cancel button -->
   <Container name="info">
      <p class="model-info">
         <span>Model file:</span> <span title={nimca.fileName} class="file-name">{nimca.fileName}</span><br>
         <span style="color:#a0a0a0;">Model:</span> {nimca.ncols} predictors{nimca.center ? ', centered' : ''}{nimca.scale ? ', scaled' : ''}
         {#if prep}
         <br><span style="color:#a0a0a0;">Preprocessing: </span>
            <PrepInfo prepmodel={prep} />
         {/if}
      </p>
      <ButtonCancel onclick={reset} />
   </Container>

{/if}

<!-- error message -->
{#if status === 'error' || errMsg}
<Container status="error">
   <p>{@html errMsg}</p>
</Container>
{/if}


<style>

   .model-info {
      line-height: 1.5em;
   }

   .model-info > :global(span) {
      vertical-align:top;
      color: #a0a0a0;
   }

   .model-info > .file-name {
      display: inline-block;
      padding: 0;
      margin: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      max-width: 25ch;
      color: #222;
   }

</style>