<script>
   /**************************************************************/
   /* ResultsTable                                               */
   /* -------------------------                                  */
   /* shows table with SIMCA results for training and PV-sets    */
   /**************************************************************/

   // main properties of the whole app
   let { res } = $props();
</script>


{#if res}
<table>
   <thead>
   <tr>
      <th></th>
      <th>Test</th>
   </tr>
   </thead>
   <tbody>
   <tr>
      <td class="label">Members</td>
      <td>{res.nMembers}</td>
   </tr>
   {#if res.nMembers > 0}
   <tr>
      <td class="label">Out</td>
      <td>{res.outcomes.rolsNum.outlier + res.outcomes.rolsNum.extreme}</td>
   </tr>
   <tr>
      <td class="label">Sensitivity</td>
      <td>{res.outcomes.stat.sensitivity.toFixed(3)}</td>
   </tr>
   {/if}

   <tr class="section">
      <td class="label">Strangers</td>
      <td>{res.nStrangers}</td>
   </tr>

   {#if res.nStrangers > 0}
   <tr>
      <td class="label">In</td>
      <td>{res.outcomes.decsNum.in - res.outcomes.rolsNum.regular}</td>
   </tr>
   <tr>
      <td class="label">Specificity</td>
      <td>{res.outcomes.stat.specificity.toFixed(3)}</td>
   </tr>
   <tr>
      <td class="label">Selectivity</td>
      <td>{res.outcomes.stat.selectivity.toFixed(3)}</td>
   </tr>
   {/if}

   {#if res.nStrangers > 0 && res.nMembers > 0}
   <tr>
      <td class="label">Efficiency</td>
      <td>{res.outcomes.stat.efficiency.toFixed(3)}</td>
   </tr>
   {/if}

   </tbody>
</table>
{/if}

<style>
   table {
      width: 100%;
      border-collapse: collapse;
   }

   tr {
      width: 100%;
   }

   th {
      border-bottom: 1px solid #a0a0a0;
   }

   tr.section > td {
      border-top: 1px solid #a0a0a0;
   }

   th, td {
      text-align: right;
      padding: 0.2em 0.5em 0.2em 0.1em;
      color: #404040;
      margin: 0;
   }

   .label {
      text-align: left;
      padding: 0.1em 0.5em;
   }
</style>